.main-header{
    background: $gradient1;    
    align-items: center;
    &__logo{
        max-width: 30%;
        min-width: 100px;
        width:160px;
        margin-right: auto;
    }

    .default-wrapper{
        padding-top: 10px;
        padding-bottom: 10px;
        align-items: center;
        @include media-breakpoint-up(lg){
            padding-top: 15px;
            padding-bottom: 15px;
        }
    }
}

.main-header__buttons{
    display: flex;
    flex-shrink: 1;
    flex-wrap: 0;
    font-size: 20px;
    a{
        color: $light-text-color;
        padding: 3px 8px;
        transition: 333ms;
        svg{
            width: 1.1em;
            height: 1.1em;
            display: block;
        }
        &:hover{
            transform: scale(1.1);
            color: #fff;
        }
    }
}

.main-menu{
    flex-grow: 1;
    font-size: 20px;
    line-height: 1.2;
    align-self: flex-end;

    display: none;
    
    @include media-breakpoint-up(md){
        margin-bottom: 10px;
        display: block;
    }
    ul{
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        list-style: none;
        margin: 0;
        padding: 0;
        a{
            text-transform: uppercase;
            padding: 0.5em .7em 0.5em;
            transition: 333ms;
            color: $light-text-color;
            text-decoration: none;
            border-radius: 5px;
            &:hover{
                color: #fff;
                background: rgba(255, 255, 255, 0.05);
                // background: $body-color;

            }
        }
    }
}