.post{
    text-decoration: none;
    // color: $light-text-color;

   display: flex;
   flex-direction: column;
   align-items: flex-start;
    

    --padding: 30px;

    &__img{
        background: #eee;
        padding-bottom: 60%;
        position: relative;
        img{
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            display: block;
            object-fit: cover;
        }
    }

    &__content{
        padding: var(--padding);
     
        .title1,
        .title2{
            font-size: 2em;
            font-weight: 800;
            margin-bottom: 0;
        }
        .title2{
            color: $accent-color;
            margin-bottom: 1em;
        }
        .more{
            font-weight: bold;
            display: block;
            margin-top: -0.9em;
        }
    }

    
    @include media-breakpoint-up(md){
        --padding: 40px;
        flex-direction: row;
        align-items: center;
        &__img{
            padding-bottom: 30%;
        }
        &__img,
        &__contnet{
            width: 50%;
            flex-grow: 0;
            flex-shrink: 0;
        }
    }
}